.range_slider {
    /* margin-top: 10px; */
}

.rangeslider-horizontal {
    height: 0.1em;
    background: #eee;
}

.rangeslider-horizontal .rangeslider__handle {
    width: 20px;
    height: 20px;

    box-shadow: none;
}

.rangeslider-horizontal .rangeslider__handle:after {
    width: 7px;
    height: 7px;
    border-radius: 100px;
    background: #c7222a;
    box-shadow: none;
    display: none;
}

.rangeslider .rangeslider__handle,
.rangeslider .rangeslider__handle:focus {
    background: #c7222a;
    border: 1px solid #c7222a !important;
    border-radius: 100px;
    box-shadow: none;
    outline: none;
}

.rangeslider-horizontal .rangeslider__fill {
    background-color: #c7222a;

}