body {
    background-color: #f7f8f9 !important;
}

.padd0 {
    padding: 0px;
}

.row {
    margin: 0 -15px !important;
}

.in-progress {
    pointer-events: none;
    text-shadow: none;
    color: rgba(255, 255, 255, 0.6) !important;
    background: -webkit-linear-gradient(-45deg, rgba(150, 159, 110, 0.5) 25%, transparent 25%, transparent 50%, rgba(150, 159, 110, 0.5) 50%, rgba(150, 159, 110, 0.5) 75%, transparent 75%, transparent), -webkit-linear-gradient(top, #3e593c, #3e593c) !important;
    background: -moz-linear-gradient(-45deg, rgba(150, 159, 110, 0.5) 25%, transparent 25%, transparent 50%, rgba(150, 159, 110, 0.5) 50%, rgba(150, 159, 110, 0.5) 75%, transparent 75%, transparent), -moz-linear-gradient(top, #3e593c, #3e593c);
    background: -ms-linear-gradient(-45deg, rgba(150, 159, 110, 0.5) 25%, transparent 25%, transparent 50%, rgba(150, 159, 110, 0.5) 50%, rgba(150, 159, 110, 0.5) 75%, transparent 75%, transparent), -ms-linear-gradient(top, #3e593c, #3e593c) !important;
    background: -o-linear-gradient(-45deg, rgba(150, 159, 110, 0.5) 25%, transparent 25%, transparent 50%, rgba(150, 159, 110, 0.5) 50%, rgba(150, 159, 110, 0.5) 75%, transparent 75%, transparent), -o-linear-gradient(top, #3e593c, #3e593c) !important;
    background: linear-gradient(-45deg, rgba(150, 159, 110, 0.5) 25%, transparent 25%, transparent 50%, rgba(150, 159, 110, 0.5) 50%, rgba(150, 159, 110, 0.5) 75%, transparent 75%, transparent), linear-gradient(to top, #3e593c, #3e593c) !important;
    -pie-background: linear-gradient(-45deg, rgba(150, 159, 110, 0.5) 25%, transparent 25%, transparent 50%, rgba(150, 159, 110, 0.5) 50%, rgba(150, 159, 110, 0.5) 75%, transparent 75%, transparent), linear-gradient(to top, #3e593c, #3e593c) !important;
    background-repeat: repeat !important;
    -webkit-background-size: 40px 40px, 100% 100% !important;
    -moz-background-size: 40px 40px, 100% 100% !important;
    -ms-background-size: 40px 40px, 100% 100% !important;
    -o-background-size: 40px 40px, 100% 100% !important;
    background-size: 40px 40px, 100% 100% !important;
    -webkit-animation: progress-bar-stripes 2s linear infinite !important;
    -moz-animation: progress-bar-stripes 2s linear infinite !important;
    -ms-animation: progress-bar-stripes 2s linear infinite !important;
    -o-animation: progress-bar-stripes 2s linear infinite !important;
    animation: progress-bar-stripes 2s linear infinite !important;
    outline: none
}

@-webkit-keyframes progress-bar-stripes {
    from {
        background-position: 0 0
    }

    to {
        background-position: 40px 0
    }
}

@-moz-keyframes progress-bar-stripes {
    from {
        background-position: 0 0
    }

    to {
        background-position: 40px 0
    }
}

@keyframes progress-bar-stripes {
    from {
        background-position: 0 0
    }

    to {
        background-position: 40px 0
    }
}

.in-progress:disabled {
    cursor: default;
    opacity: .5
}

.in-progress:hover {
    color: rgba(255, 255, 255, 0.6) !important;
}

.padd0 {
    padding: 0px;
}

.body_loader {
    width: 100%;
    height: 60vh;
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.75);
}

.loading_div {
    position: absolute;
    width: 90px;
    height: 90px;
    /* border: 1px solid red; */
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    z-index: 999;
    display: block;
    bottom: 0;
    /* background: url('../../images/Loader.gif') no-repeat center center; */
}