.imgthumb {
    width: 100%;
    height: 150px;
    position: relative;
    overflow: hidden;
}

.imgthumb img {
    margin: 0px auto;
    display: block;
    max-width: 100%;
}

.mediaBank_name {
    font-size: 23px;
    color: #2e2e2e;
    margin: 0px 0 10px 0px;
    font-family: 'pf_handbook_proregular';
    text-transform: uppercase;
}

.bank-sp {
    display: flex;
    margin: 16px 0 10px;
    font-size: 14px;
    color: #2e2e2e;
}

.bank-sp h6 {
    margin: 0px;
    padding: 0px;
    width: 40%;
}

.bank-footer-details {
    color: #2e2e2e;
    display: block;
    margin-top: 4px;
    justify-content: space-between;
    font-family: 'pf_handbook_proregular';
}

.loanDesc {
    font-size: 16px;
    font-family: 'pf_encore_sans_probook';
    font-weight: 400;
    line-height: 1.5;
    text-align: left;
    display: inline-table;
    width: 40%;
    color: #212529;

}

.loanamount {
    text-align: right;
}